<template>
  <div class="col-sm-12" style="overflow:auto;">
    <div class="tops" v-bind:style="{ height: topheight }">
      <exploration-select v-ref:queryuser v-show="show"></exploration-select>
      <modify-user-files v-ref:modifyuser v-show="!show"></modify-user-files>
    </div>
    <div v-show="showtotal" v-bind:style="{ height: botheight }">
      <div class="col-sm-2" v-show="showbasic" >
        <history_control v-ref:binfo :selectdata="selectdata"></history_control>
      </div>
      <div class="col-sm-10">
        <tabset v-ref:tabs :close="false">
          <tab :header='header'>
            <service-control  v-ref:service :selectdata="selectdata" ></service-control>
          </tab>
          <!--<tab header='档案信息管理'>-->
            <!--<record-message v-ref:record :selectdata="selectdata" :key="tittle1"-->
                                     <!--:edit = "true">-->
            <!--</record-message>-->
          <!--</tab>-->

          <!--<tab header='材料信息'>-->
            <!--<material-message v-ref:material :selectdata="selectdata"-->
                                       <!--:edit = "true">-->
            <!--</material-message>-->
          <!--</tab>-->
          <tab header='附件'>
            <image-upload v-ref:file :blodid = "selectdata.f_process_id" :isusetype = "true" :f_usetype="'附件上传'"  :edit="true" :isremark = "true"  :defname="selectdata.defname"></image-upload>
            <!--<customer-file v-ref:file :selectdata="selectdata"></customer-file>-->
          </tab>
          <!--<tab header='设备新增' v-if="flagg">-->
            <!--<equipment-message v-ref:equipment :selectdata="selectdata"-->
                                        <!--:edit = "true">-->
            <!--</equipment-message>-->
          <!--</tab>-->
        <!--  <tab header='设备管理' v-if="flagg">
            <customer-record-message v-ref:record :selectdata="selectdata" :key="tittle3"></customer-record-message>
          </tab>-->
        </tabset>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import {HttpResetClass} from 'vue-client'
  export default {
    title: '报建流程',
    data() {
      return {
        flagg: false,
        show: true,
        showbasic: false,
        showtotal: false,
        header: null,
        selectdata: {},
        botheight: '0%',
        topheight: '100%',
        tittle1:'户档案管理',
        tittle2:'表档案管理',
        tittle3:'设备管理'
      }
    },
   async created(){
      console.log(this.$login.f)
     let http = new HttpResetClass()
      http.load('POST','rs/logic/ApplyGetConfigs', {data:this.$login.f}, {resolveMsg: null, rejectMsg: null}
     ).then((res)=>{
       if(res.data.code && res.data.code == 200){
         Vue.workflow_vue = Vue.prototype.$workflow_vue = res.data.workflow_vue
       }else{
         this.$showMessage('获取报建配置失败,原因:' + res.data.msg)
         return
       }
       this.$refs.queryuser.showselect = false
       this.$nextTick(() => {
         this.$refs.queryuser.showselect = true
       })
     })
      console.log("Vue.$workflow_vue:"+JSON.stringify(this.$workflow_vue))
    },
    events: {
      'addactive'() {
        this.$refs.queryuser.$refs.cp.$refs.cri.search()
        this.hidden()
        this.showbasic = true
        this.topheight = '20%'
        this.botheight = '80%'
        this.$refs.queryuser.buttonmsg = '返回'
      },
      'changeheight'() {
        this.show = true
        this.botheight = '0%'
      },
      'check'(val) {
        this.selectdata = null
        this.selectdata = val
        console.log("节点编号:"+this.selectdata.actdefid,"节点名称:"+this.selectdata.defname)
        this.topheight = '20%'
        this.botheight = '80%'
        this.$refs.queryuser.buttonmsg = '返回'
        this.$refs.queryuser.$refs.cp.$refs.grid.model.rows = [val]
        this.$refs.queryuser.$refs.cp.pager = false
        this.showbasic = true
        this.header = val.defname
        this.hidden()
        this.$refs.service.refurbish(val)
      },
      'selfsearch'() {
        this.$refs.queryuser.$refs.cp.$refs.cri.search()
      },
      'close'() {
        this.showtotal = false
        this.topheight = '100%'
        this.botheight = '0%'
        this.$refs.queryuser.buttonmsg = '查询'
        this.$refs.queryuser.$refs.cp.pager = true
        this.$refs.queryuser.$refs.cp.$refs.cri.search()
      }
    },
    methods: {
      hidden() {
        this.showtotal = true
      },
      close() {
        this.hidden()
        this.topheight = '100%'
        this.botheight = '0%'
        this.$refs.queryuser.buttonmsg = '查询'
      }
    },
    watch: {
      'selectdata'(val) {
        console.log('selectdata:', val)
      },
      'selectdata.f_customer_type'() {
        if (this.selectdata.f_customer_type == '企业') {
          this.flagg = true
        }
      }
    }
  }
</script>



